.gpt3_footer{
    display: flex;
    color: white;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}
.gpt3_footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}
.gpt3_footer-heading h1{
    font-family: var(--font-family);
font-size: 62px;
font-weight: 800;
line-height: 75px;
letter-spacing: -0.04em;
text-align: center;

}
.gpt3_footer-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.gpt3_footer-btn p{
   
font-family: var(--font-family);
font-size: 18px;
line-height: 21px;
word-spacing: 2px;

color: #ffffff;

}
.gpt3_footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.gpt3_footer-links div{
    width: 250px;
    margin: 1rem;
}
.gpt3_footer-links_logo{
    display: flex;
    flex-direction: column;
}
.gpt3_footer-links_logo img{
    width: 118px;
    height: 100px;
    margin-bottom: 1rem;

}




.gpt3_footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #ffffff;
}

.gpt3_footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

}
.gpt3_footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #ffffff;
    margin-bottom: 1rem;
}
.gpt3_footer-links_div p{
    font-family: var(--font-family);
    margin: 0.5rem 0;
    cursor: pointer;
    font-size: 12px;
    line-height: 14px;
    color: #ffffff;

}
.gpt3_footer-copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;

}
.gpt3_footer-copyright p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #ffffff;
}

@media screen and (max-width:850px){
    .gpt3_footer-heading{
        font-size: 44px;
        line-height: 50px;
    }

}

@media screen and (max-width:550px){
    .gpt3_footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3_footer-links div{
        margin: 1rem 0;

    }
    .gpt3_footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
    
}

@media screen and (max-width:350px){
    .gpt3_footer-heading h1{
        font-size: 27px;
        line-height: 38px;
    }
  
    
}