.gpt3_whatgpt3 {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt3_whatgpt3-feature{
    display: flex;
}

.gpt3_whatgpt3-feature .gpt3_features-container-feature{
    margin: 0;
}
.gpt3_whatgpt3-feature .gpt3_features-container-feature-text{
    max-width: 700px;

}
.gpt3_whatgpt3-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
    
}
.gpt3_whatgpt3-heading h1{
    font-size: 34px;
    line-height: 45px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    max-width: 510px;
}
.gpt3_whatgpt3-heading p{
    font-size: 16px;
    line-height: 30px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    color: var(--color-subtext);
}

.gpt3_whatgpt3-container{
    display: flex;
    
    margin-top: 2rem;
}
.gpt3_whatgpt3-container .gpt3_features-container_feature {
    flex-direction: column;
}

@media screen and (max-width: 650px) {

    .gpt3_whatgpt3-container{
        flex-wrap: wrap;
    }
   
    .gpt3_whatgpt3-feature .gpt3_features-container_feature {
        flex-direction: column;
    }

   
    .gpt3_whatgpt3-feature .gpt3_features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3_whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    .gpt3_whatgpt3-container{
        flex-wrap: wrap;
    }
    
    .gpt3_whatgpt3-container .gpt3_features-container_feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}