.gpt3_cta{
   
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.gpt3_cta-content p{
font-family: var(--font-family);
font-size: 12px;
font-weight: 500;
line-height: 30px;
letter-spacing: 0em;
text-align: left;
color: #0e0e0e;
}
.gpt3_cta-content h3{
    font-family: var(--font-family);
font-size: 24px;
font-weight: 800;
line-height: 45px;
letter-spacing: 0em;
text-align: left;

}
.gpt3_cta-button{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 2rem;

}
.gpt3_cta-button button{
    width: 189.14px;
height: 59.4px;

border-radius: 40px;
outline: none;
border: none;
background: #000;
color: #ffffff;
font-family: var(--font-family);
font-size: 18px;
font-weight: 700;
line-height: 30px;
letter-spacing: 0em;
cursor: pointer;
min-width: 150px;



}

@media screen and (max-width:650px){
    .gpt3_cta{
        flex-direction: column;
    }
    .gpt3_cta-button{
        margin: 2rem 0 0;
    }
}
@media screen and (max-width:550px){
    .gpt3_cta{
      margin: 4rem 2rem;
    }
    .gpt3_cta-content h3{
     font-size: 18px;
     line-height: 32px;
    }
    .gpt3_cta-btn button{
        font-size: 14px;
        line-height: 28px;
    }
}