.gpt3_header{
    display: flex;

}
.gpt3_header-content{
    flex: 1.5;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}
.gpt3_header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}
.gpt3_header-content p{
    font-family: var(--font-family);
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    letter-spacing: -0.04rem;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.gpt3_header-content_input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;

}
.gpt3_header-content_input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    border: 2px solid var(--color-footer);
    background: #052D56;
    font-family: var(--font-family);
    font-weight: normal;
    font-style: normal;
    font-size: 20px;
    line-height: 27px;
    color: #fff;
    padding: 0rem 1rem;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

}

.gpt3_header-content_input button{
    background: #ff4820;
    width: 100%;
    flex: 0.6;
    min-height: 50px;
    cursor: pointer;
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    outline: none;
    padding: 0rem 1rem;
    border: none;
    font-family: var(--font-family);
    color: #ffffff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

}
.gpt3_header-content_people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.gpt3_header-content_people img{
    width: 181.79px;
    height: 30px;
}
.gpt3_header-content_people p{
    font-family: var(--font-family);
    line-height: 38px;
    font-size: 12px;
    color: #ffffff;
    font-weight: 500;
    margin: 0 0 0 1rem;
    text-align: center;

}

.gpt3_header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}
.gpt3_header-image img{
    width: 100%;
    height: 100%;

}




@media screen and (max-Width:1050px) {
.gpt3_header{
    flex-direction: column;
}
.gpt3_header-content{
    margin: 0 0 3rem;
}
}

@media screen and (max-Width:650px) {
.gpt3_header h1{
    font-size: 48px;
    line-height: 60px;
}
.gpt3_header p{
    font-size: 16px;
    line-height: 24px;
}
.gpt3_header-content_people{
    flex-direction: column;
}
.gpt3_header-content_people p{
    margin: 0;
}
.gpt3_header-content_input input,
.gpt3_header-content_input button{
    font-size: 16px;
    line-height: 24px;
}
}
@media screen and (max-Width:490px) {
    .gpt3_header h1{
        font-size: 36px;
        line-height: 48px;
    }
    .gpt3_header p{
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3_header-content_input input,
.gpt3_header-content_input button{
    font-size: 12px;
    line-height: 16px;
}
}